.header {
	background-color: var(--dark-blue);
}

.header__content {
	max-width: 768px;
	margin-left: auto;
	margin-right: auto;
	padding: 15px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.header__brand {
	max-width: 300px;
	-ms-flex: 3 1;
	    flex: 3 1;
	margin: 0;
}

.header__content > .nav {
	-ms-flex: 2 1;
	    flex: 2 1;
}


